import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import JobCostRolloverUpdateForm from "./updateForm";
import JobCostRolloverDocUpdateForm from "./updateDocForm";
import History from "./history";


import notification from "../../../../components/notification";
import { Spin, Tooltip } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/inventory/jobCostRollover/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";
import Print from "./print";

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/inventories/job-cost-rollovers";

class UpdateJobCostRollover extends Component {

  state = {
    visible: false
  }
  // Get All data diawal
  componentDidMount() {
    const { editJobCostRollover } = this.props;
    const { id } = this.props.match.params;
    editJobCostRollover(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.jobCostRollover.error) {
      notification("error", this.props.jobCostRollover.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.jobCostRollover.success) {
      notification("success", this.props.jobCostRollover.success);
      this.props.clearSuccess();
    }
  }

  handleSubmit = value => {
    const { updateJobCostRollover } = this.props;
    const { id } = this.props.match.params;

    updateJobCostRollover(id, value);
  };

  handleSubmitCancel = value => {
    const { cancelJobCostRollover } = this.props;
    const { id } = this.props.match.params;

    cancelJobCostRollover(id, value);
  }

  handleModalHistory = () => {
    this.setState({ visible: !this.state.visible })
  };

  render() {
    // console.log(this.props);
    const { job_cost_rollover, loading } = this.props.jobCostRollover
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    return (
      <LayoutWrapper> 
        <Box>
          <PageHeader title={<IntlMessages id="jobCostRollovers.update.title" />}
            style={{ margin:0 }}
          >
             <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemRequests.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={job_cost_rollover} />

            <Button 
              style={{ marginLeft: "5px" }} 
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <History
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={job_cost_rollover ? job_cost_rollover.description : null}
          />

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="jobCostRollovers.tab.detail" />} 
              key="1" 
            >
              {this.props.jobCostRollover.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <JobCostRolloverUpdateForm
                  submit={this.handleSubmit}
                  jobCostRollover={this.props.jobCostRollover}
                  permissions={permissions}
                  processJobCostRollover={this.props.processJobCostRollover}
                  handleSubmitCancel={this.handleSubmitCancel}
                  getItems={this.props.getItems}
                />    
              )}
            </TabPane>
            <TabPane tab={<IntlMessages id="jobCostRollovers.tab.attachment" />} 
              key="2"
            >  
             {this.props.jobCostRollover.loadingEdit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <JobCostRolloverDocUpdateForm
                  submit={this.handleSubmit}
                  jobCostRollover={this.props.jobCostRollover}
                  downloadDocument={this.props.downloadDocument}
                />    
              )}
            </TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { 
    jobCostRollover: state.jobCostRollover, 
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateJobCostRollover);
