import React, { Component } from "react";
import { Row, Col, Divider } from "antd";

import IntlMessages from "../../../../components/utility/intlMessages";
import PrintTable from "../../../Tables/antTables/PrintTable.style";
import PrintTransaction, {
  PrintField,
  FieldCol
} from "../../../App/PrintTransaction";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import moment from "moment";
import { TotalField, Signature } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

class Print extends Component {
  state = {
    total_cost: 0,
    total_expense: 0,
    total_amount: 0,
    total_cost_ratio: 0,
    total_expense_ratio: 0,
    total_amount_ratio: 0
  };

  componentDidUpdate = prevProps => {
    if (prevProps.data !== this.props.data && this.props.data) {
      const data = this.props.data;
      let total_cost = 0;
      let total_cost_ratio = 0;
      let total_expense = 0;
      let total_expense_ratio = 0;

      data.job_cost_rollover_details.map(x => {
        total_cost += parseFloat(x.allocated_amount);
        total_cost_ratio += parseFloat(x.allocated_ratio);
      });

      data.expenses.map(x => {
        total_expense += parseFloat(x.allocated_amount);
        total_expense_ratio += parseFloat(x.allocated_ratio);
      });

      this.setState({
        total_cost,
        total_cost_ratio,
        total_expense,
        total_expense_ratio
      });
    }
  };

  render() {
    let columnItem = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        align: "right"
      },
      {
        title: getLanguage({ id: "items.code" }),
        dataIndex: "item_code"
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        render: text => {
          if (text && text.length > 50) {
            return text.substring(0, 50) + "...";
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "qty" }),
        dataIndex: "base_qty",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "sidebar.unit" }),
        dataIndex: "base_unit_code",
        align: "center"
      },
      {
        title: getLanguage({ id: "cost" }),
        dataIndex: "cost_per_unit",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "subTotal" }),
        dataIndex: "allocated_amount",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "ratio%" }),
        dataIndex: "allocated_ratio",
        align: "right",
        render: text => <NumberFormat value={text} />
      }
    ];

    let columnExpense = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        align: "right"
      },
      {
        title: getLanguage({ id: "accountCode" }),
        dataIndex: "account_code",
        render: text => {
          if (text && text.length > 50) {
            return text.substring(0, 50) + "...";
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "accountName" }),
        dataIndex: "account_name",
        render: text => {
          if (text && text.length > 50) {
            return text.substring(0, 50) + "...";
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "baseAmount" }),
        dataIndex: "allocated_amount",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "ratio%" }),
        dataIndex: "allocated_ratio",
        align: "right",
        render: text => <NumberFormat value={text} />
      }
    ];

    const data = this.props.data ? this.props.data : [];
    const { loading } = this.props;
    //console.log(this.state)
    const leftSide = [
      {
        label: <IntlMessages id="jobCostingDate" />,
        description: data.job_costing
          ? moment(
              data.job_costing.transaction_date,
              "YYYY-MM-DD HH:mm:ss"
            ).format("DD MMMM YYYY, HH:mm:ss")
          : null
      },
      {
        label: <IntlMessages id="jobCostingLocation" />,
        description: data.job_costing ? data.job_costing.location_name : null
      },
      {
        label: <IntlMessages id="GLAccount" />,
        description: data.job_costing
          ? data.job_costing.account_code +
            " (" +
            data.job_costing.account_name +
            ")"
          : null
      },
      {
        label: <IntlMessages id="rolloverTo" />,
        description:
          data.rollover_type === 1 ? (
            <IntlMessages id="item" />
          ) : (
            <IntlMessages id="account" />
          )
      },
      {
        label: <IntlMessages id="rolloverDate" />,
        description: data.rollover_date
          ? moment(data.rollover_date, "YYYY-MM-DD HH:mm:ss").format(
              "DD MMMM YYYY, HH:mm:ss"
            )
          : null
      },
      {
        label: <IntlMessages id="rolloverLocation" />,
        description: data.location_name
      }
    ];

    const rightSide = [
      {
        label: <IntlMessages id="note" />,
        description: data.note
      }
    ];

    const addition = [
      {
        label: <IntlMessages id="totalCostRatio" />,
        amount: (
          <>
            <NumberFormat value={this.state.total_cost_ratio} />
            {"%"}
          </>
        ),
        parseAmount: false
      },
      {
        label: <IntlMessages id="totalExpenseRatio" />,
        amount: (
          <>
            <NumberFormat value={this.state.total_expense_ratio} />
            {"%"}
          </>
        ),
        parseAmount: false
      },
      {
        label: <IntlMessages id="totalAmountRatio" />,
        amount: (
          <>
            <NumberFormat
              value={
                this.state.total_cost_ratio + this.state.total_expense_ratio
              }
            />
            {"%"}
          </>
        ),
        parseAmount: false
      }
    ];

    const additionRolloverExpense = [
      {
        label: <IntlMessages id="totalExpenseRatio" />,
        amount: (
          <>
            <NumberFormat value={this.state.total_expense_ratio} />
            {"%"}
          </>
        ),
        parseAmount: false
      },
      {
        label: <IntlMessages id="totalAmountRatio" />,
        amount: (
          <>
            <NumberFormat
              value={
                this.state.total_cost_ratio + this.state.total_expense_ratio
              }
            />
            {"%"}
          </>
        ),
        parseAmount: false
      }
    ];

    const total = [
      {
        label: <IntlMessages id="totalCost" />,
        amount: <NumberFormat value={this.state.total_cost} />
        // parseAmount: true
      },
      {
        label: <IntlMessages id="totalExpense" />,
        amount: <NumberFormat value={this.state.total_expense} />
        // parseAmount: true
      },
      {
        label: <IntlMessages id="totalAmount" />,
        amount: data.job_costing ? (
          <NumberFormat value={data.job_costing.total_amount} />
        ) : (
          0
        )
        // parseAmount: true
      }
    ];

    const totalRolloverExpense = [
      {
        label: <IntlMessages id="totalExpense" />,
        amount: <NumberFormat value={this.state.total_expense} />
        // parseAmount: true
      },
      {
        label: <IntlMessages id="totalAmount" />,
        amount: data.job_costing ? (
          <NumberFormat value={data.job_costing.total_amount} />
        ) : (
          0
        )
        // parseAmount: true
      }
    ];

    const signatureData = [
      {
        label: <IntlMessages id="createdBy" />
      },
      {
        label: <IntlMessages id="confirmedBy" />
      }
    ];
    return (
      <>
        <PrintTransaction
          className="print-transaction"
          componentRef={ref => (this.componentRef = ref)}
          title={<IntlMessages id="sidebar.jobCosting" />}
          subtitle={
            <>
              <Row>
                <Col>
                  <strong>{data.job_costing_no}</strong>
                </Col>
              </Row>
              <Row style={{ marginTop: "10px" }}>
                <Col span={24}>
                  <PrintField
                    bold={true}
                    label={<IntlMessages id="date" />}
                    description={
                      data.rollover_date
                        ? moment(
                            data.rollover_date,
                            "YYYY-MM-DD HH:mm:ss"
                          ).format("DD MMMM YYYY, HH:mm:ss")
                        : null
                    }
                  />
                </Col>
              </Row>
            </>
          }
        >
          {/* Bagian list detail */}
          <Row style={{ marginTop: "24px" }}>
            <FieldCol leftSide={leftSide} rightSide={rightSide} />
          </Row>

          {/* Bagian Table */}

          {data.rollover_type === 1 ? (
            <Row style={{ marginTop: "24px" }}>
              <Divider
                orientation="left"
                style={{
                  fontSize: "12px",
                  alignItems: "center",
                  display: "flex"
                }}
              >
                <IntlMessages
                  id="jobCostings.tab.item"
                  style={{ marginTop: "-10" }}
                />
              </Divider>
              <Col span={24}>
                <PrintTable
                  columns={columnItem}
                  dataSource={data.job_cost_rollover_details}
                  rowKey={row => row.id_job_cost_rollover_detail}
                  pagination={false}
                />
              </Col>
            </Row>
          ) : null}

          <Row style={{ marginTop: "24px" }}>
            <Divider
              orientation="left"
              style={{
                fontSize: "12px",
                alignItems: "center",
                display: "flex"
              }}
            >
              <IntlMessages
                id="jobCostings.tab.expense"
                style={{ marginTop: "-10" }}
              />
            </Divider>
            <Col span={24}>
              <PrintTable
                columns={columnExpense}
                dataSource={data.expenses}
                rowKey={row => row.id_job_cost_rollover_expense}
                pagination={false}
              />
            </Col>
          </Row>

          <Row type="flex" style={{ marginTop: "24px" }}>
            <Col span={12}>
              <TotalField
                data={
                  data.rollover_type === 1 ? addition : additionRolloverExpense
                }
              />
            </Col>
            <Col span={12}>
              <TotalField
                data={data.rollover_type === 1 ? total : totalRolloverExpense}
              />
            </Col>
          </Row>

          <Row style={{ marginTop: "24px" }}>
            <Col span={24}>
              <Signature data={signatureData} />
            </Col>
          </Row>
        </PrintTransaction>

        <ReactToPrint
          trigger={() => (
            <ButtonPrint loading={loading} style={{ marginLeft: "5px" }} />
          )}
          content={() => this.componentRef}
          onBeforePrint={() =>
            (document.title = getFileName(
              "job_costing",
              data.job_costing_no,
              true
            ))
          }
          onAfterPrint={() => (document.title = siteConfig.documentTitle)}
        />
      </>
    );
  }
}

export default Print;
