import React, { Component } from "react";
import moment from "moment"
import EditableTableExpense from "./editableTableExpense";
import EditableTableItem from "./editableTable";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form, Input, Select, Col, InputNumber, DatePicker,
  Popconfirm, Steps, Row, Tabs, Divider, Spin, Empty, Radio
} from "antd";
import { customDot } from "../../../App/app.style";
import notification from "../../../../components/notification";
import ModalCancel from "./modalCancel";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const TabPane = Tabs.TabPane;
const { Option } = Select;
const InputGroup = Input.Group;
const TextArea = Input.TextArea;
const Step = Steps.Step;

class JobCostRolloverUpdateForm extends Component {
  state = {
    check: 1,
    id_job_costing: null,
    temp_job_cost_rollover_detail: null,
    temp_job_cost_rollover_details: [],
    job_cost_rollover_details: [],
    temp_expense: null,
    temp_expenses: [],
    expenses: [],
    total_cost: 0,
    total_expense: 0,
    total_amount: 0,
    ratio_cost: 0,
    ratio_expense: 0,
    ratio_amount: 0,
    visibleModalJobCostRollover: false,
    id_location: null,
    search: null,
    clear: false,
    visibleModalCancel: false,
    jc_items: [],
  }

  componentDidUpdate = () => {
    if (this.state.check === 1 && this.props.jobCostRollover.job_cost_rollover !== null) {
      const { job_cost_rollover, items } = this.props.jobCostRollover;
      const { setFieldsValue } = this.props.form;
      const { getItems } = this.props;


      setFieldsValue({
        job_costing_date: moment(job_cost_rollover.job_costing.transaction_date, "YYYY-MM-DD HH:mm:ss"),
        id_location_job_costing: job_cost_rollover.job_costing.id_location,
        id_gl_account: job_cost_rollover.job_costing.id_gl_account,
        job_costing_no: job_cost_rollover.job_costing.job_costing_no,
        total_amount: parseFloat(job_cost_rollover.job_costing.total_amount),
        id_location: job_cost_rollover.id_location,
        rollover_date: moment(job_cost_rollover.rollover_date, "YYYY-MM-DD HH:mm:ss"),
        rollover_type: job_cost_rollover.rollover_type,
        note: job_cost_rollover.note
      })

      getItems(job_cost_rollover.id_location)

      let job_cost_rollover_details = [];
      let expenses = [];
      let total_cost = 0;
      let total_expense = 0;


      job_cost_rollover.job_cost_rollover_details.map((data, key) => {
        let job_cost_rollover_detail = {
          index_no: data.index_no,
          id_item: data.id_item,
          item_code: data.item_code,
          item_name: data.item_name,
          base_qty: parseFloat(data.base_qty),
          id_base_unit: data.id_base_unit,
          base_unit_code: data.base_unit_code,
          cost_per_unit: parseFloat(data.allocated_amount / data.base_qty),
          allocated_amount: parseFloat(data.allocated_amount),
          allocated_ratio: parseFloat(data.allocated_ratio),
          id_department: data.id_department,
          id_project: data.id_project,
        }
        job_cost_rollover_details.push(job_cost_rollover_detail);
        total_cost += parseFloat(data.allocated_amount)
      })

      job_cost_rollover.expenses.map((data, key) => {
        let expense = {
          index_no: data.index_no,
          id_account: data.id_account,
          account_code: data.account_code,
          account_name: data.account_name,
          allocated_amount: parseFloat(data.allocated_amount),
          allocated_ratio: parseFloat(data.allocated_ratio),
        }
        expenses.push(expense);
        total_expense += parseFloat(data.allocated_amount)
      })

      //console.log(job_cost_rollover_details);
      //untuk item pengecualian
      let jc_items = [];

      job_cost_rollover.job_costing.job_costing_details.map((detail) => {
        jc_items.push(detail.id_item);
      })

      this.setState({
        id_job_costing: job_cost_rollover.id_job_costing,
        temp_job_cost_rollover_details: job_cost_rollover_details,
        temp_expenses: expenses,
        expenses: expenses,
        total_cost,
        total_expense,
        //total_amount : total_cost + total_expense, 
        total_amount: job_cost_rollover.job_costing.total_amount,
        id_location: job_cost_rollover.id_location,
        check: 0,
        jc_items
      })

    }
  }

  handleModalCancel = () => {
    this.setState({
      visibleModalCancel: !this.state.visibleModalCancel
    })
  }

  handleSave = (job_cost_rollover_details, total_cost) => {
    //console.log(total_cost)
    this.setState({
      job_cost_rollover_details: job_cost_rollover_details,
      total_cost,
    });
  };

  clearJobCostRolloverDetail = (job_cost_rollover_details, total_cost) => {
    //console.log(total_cost)
    this.setState({
      temp_job_cost_rollover_detail: null,
      job_cost_rollover_details: job_cost_rollover_details,
      total_cost,
      clear: false
    });
  };

  clearJobCostRolloverDetails = (job_cost_rollover_details, total_cost) => {
    //console.log(total_cost)
    this.setState({
      temp_job_cost_rollover_details: [],
      job_cost_rollover_details: job_cost_rollover_details,
      total_cost,
      clear: false
    });
  };

  handleSaveExpense = (expenses, total_expense) => {
    this.setState({
      expenses: expenses,
      total_expense,
    });
  };

  clearExpense = (expenses, total_expense) => {

    this.setState({
      temp_expense: null,
      expenses: expenses,
      total_expense,
    });
  };

  clearExpenses = (expenses, total_expense) => {

    this.setState({
      temp_expenses: [],
      expenses: expenses,
      total_expense,
    });
  };

  handleChangeExpenseAccount = (e) => {
    const { accounts } = this.props.jobCostRollover;
    const [account] = accounts.filter(
      data => data.id_account === e
    );
    this.setState({
      temp_expense: account
    });

  }

  handleSelectLocation = id_location => {
    const { getItems } = this.props;
    this.setState({
      id_location: id_location,
      clear: this.state.id_location !== id_location ? true : false
    });

    getItems(id_location);
  };



  handleSelectItem = value => {
    const { items } = this.props.jobCostRollover;
    const { search, id_location } = this.state;
    const { getItems } = this.props;
    // console.log(items.filter(item => item.id_item === value));
    this.setState({
      temp_job_cost_rollover_detail: items.filter(item => item.id_item === value)[0]
    });

    if (search) {
      getItems(id_location);
      this.setState({ search: null });
    }
  };

  handleSearchItem = value => {
    const { getItems } = this.props;
    const { id_location } = this.state;
    // console.log(this.props.purchaseInvoice);
    this.setState({ search: value });
    if (id_location) {
      getItems(id_location, value);
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      const { messages } = this.props.jobCostRollover
      const { getFieldValue } = this.props.form
      // console.log(err)
      // console.log(values)
      //console.log(this.state.total_amount - (this.state.total_cost + this.state.total_expense));
      if (getFieldValue("rollover_type") === 1 && parseFloat((this.state.total_amount - (this.state.total_cost + this.state.total_expense)).toFixed(2)) !== 0.00) {
        notification("error", messages.allocated_ratio)
      }
      else if (getFieldValue("rollover_type") === 2 && parseFloat((this.state.total_amount - this.state.total_expense).toFixed(2)) !== 0.00) {
        notification("error", messages.allocated_ratio)
      }
      else if (getFieldValue("rollover_type") === 1 && this.state.job_cost_rollover_details.length < 1) {
        notification("error", messages.item_required)
      }
      else if (
        getFieldValue("rollover_type") === 1 && this.state.job_cost_rollover_details.length > 0 && 
        this.state.total_cost === 0
      ) {
        notification("error", messages.item_required)
      }
      else if (!err) {
        if (getFieldValue("rollover_type") === 1) {
          values.job_cost_rollover_details = this.state.job_cost_rollover_details;
        }
        else {
          values.job_cost_rollover_details = [];
        }
        values.id_job_costing = this.state.id_job_costing
        values.rollover_date = values.rollover_date.format("YYYY-MM-DD HH:mm:ss");
        values.expenses = this.state.expenses;
        this.props.submit(values);
      }
    });
  };


  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { jobCostRollover, processJobCostRollover } = this.props;
    const { loading } = jobCostRollover;
    const { search } = this.state;

    const data = jobCostRollover.job_cost_rollover ? jobCostRollover.job_cost_rollover : [];

    let permissions = this.props.permissions;

    // const error = jobCostRollover.errorField ? jobCostRollover.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalCancel
          visible={this.state.visibleModalCancel}
          handleModal={this.handleModalCancel}
          submit={this.props.handleSubmitCancel}
          loading={loading}
        />
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
           <IntlMessages id="jobCostRollovers.add.subtitle.data" />
         </Divider> */}
        <Row style={{ margin: "24px 0px" }} >
          <Col span={16} justify="center">
            <Steps size="small"
              progressDot={customDot}
              current={
                data.status - 1
              }
            >
              <Step title={getLanguage({id: "new"})} />
              <Step title={getLanguage({id: "processed"})} />
              <Step title={getLanguage({id: "cancelled"})} />
            </Steps>
          </Col>
        </Row>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div style={{ padding: "0 15px 0 0" }} className="isoColumn-2 isoFormGroupComp">
            <InputGroup>
              <Col span={24}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="jobCostingNo" />}
                >
                  {getFieldDecorator("job_costing_no", {
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(<Input
                    disabled={true}
                  />)}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="jobCostingDate" />
                  }
                >
                  {getFieldDecorator("job_costing_date", {
                    //initialValue: moment(),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime
                      format="DD MMMM YYYY HH:mm:ss"
                      // onChange={this.handleChangeTransDate}
                      // onOpenChange={this.handleChangeTransDate}
                      disabled={true}
                      allowClear={false}
                      disabledDate={e => e.valueOf() > moment()}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="jobCostingLocation" />}
                >
                  {getFieldDecorator("id_location_job_costing", {
                    // initialValue: data.id_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.jobCostRollover.loading}
                      showSearch
                      optionFilterProp="children"
                      onChange={this.handleSelectLocation}
                      disabled={true}
                      style={{ width: "100%" }}
                    >
                      {jobCostRollover.locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="GLAccount" />}
                >
                  {getFieldDecorator("id_gl_account", {
                    // initialValue: data.id_gl_account,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.jobCostRollover.loading}
                      showSearch
                      optionFilterProp="children"
                      disabled={true}
                      style={{ width: "100%" }}

                    >

                      {jobCostRollover.accounts.map((data, key) => (
                        // data.id_account_type === "04" ||
                        // data.id_account_type === "15" ||
                        // data.id_account_type === "16" ||
                        // data.id_account_type === "18"  
                        // ?
                        (
                          <Option key={key} value={data.id_account}>
                            {data.account_code + " (" + data.account_name + ")"}
                          </Option>
                        )
                        //: null   
                      ))
                      }
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="rolloverTo" />}
                >
                  {getFieldDecorator("rollover_type", {
                    //initialValue: data.cash_flow_type,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Radio.Group
                      buttonStyle="solid"
                      disabled={data.status === 1 ? false : true}
                      style={{ display: "flex" }}
                    >
                      <Radio.Button style={{ flex: "1", textAlign: "center" }} value={1}><IntlMessages id="item" /></Radio.Button>
                      <Radio.Button style={{ flex: "1", textAlign: "center" }} value={2}><IntlMessages id="account" /></Radio.Button>
                    </Radio.Group>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>


            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="rolloverDate" />
                  }
                >
                  {getFieldDecorator("rollover_date", {
                    initialValue: moment(),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime
                      format="DD MMMM YYYY HH:mm:ss"
                      // onChange={this.handleChangeTransDate}
                      // onOpenChange={this.handleChangeTransDate}
                      disabled={
                        !permissions.includes("Can Back Date")
                          ? true
                          : false
                      }
                      allowClear={false}
                      disabledDate={e => e.valueOf() > moment()}
                      disabled={data.status === 1 ? false : true}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="rolloverLocation" />}
                >
                  {getFieldDecorator("id_location", {
                    // initialValue: data.id_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.jobCostRollover.loading}
                      showSearch
                      optionFilterProp="children"
                      onChange={this.handleSelectLocation}
                      style={{ width: "100%" }}
                      disabled={data.status === 1 ? false : true}
                    // disabled={used ? true : false}
                    >
                      {jobCostRollover.locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>


          </div>
          <div style={{ padding: "0 0 0 15px" }} className="isoColumn-2 isoFormGroupComp">

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="note" />}
            >
              {getFieldDecorator("note", {
                //initialValue: data.description,
              })(<TextArea
                autosize={{ minRows: 4 }}
                disabled={data.status === 1 ? false : true}
              />)}
            </Form.Item>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="totalAmount" />}
                >
                  {getFieldDecorator("total_amount", {
                    //initialValue: data.amount,
                    // initialValue: 0, 
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      min={0.01}
                      precision={2}
                      disabled={true}
                      style={{ width: "100%" }}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="unallocatedAmount" />}
                >
                  <InputNumber
                    value={
                      getFieldValue("rollover_type") === 1
                        ? parseFloat((this.state.total_amount - (this.state.total_cost + this.state.total_expense)).toFixed(2))
                        : parseFloat(this.state.total_amount - this.state.total_expense).toFixed(2)
                    }
                    formatter={value =>
                      `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                    }
                    parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    //min={0.01}
                    precision={2}
                    disabled={true}
                    style={{ width: "100%" }}
                  />
                  {/* )} */}
                </Form.Item>
              </Col>
            </InputGroup>

          </div>
        </div>

        <Divider orientation="left" style={{ fontSize: 14, marginBottom: 0 }}>
          {
            getFieldValue('rollover_type') === 2
              ? (<IntlMessages id="jobCostRollovers.subtitle.expenses" />)
              : (<IntlMessages id="jobCostRollovers.subtitle.itemsExpenses" />)
          }
        </Divider>



        <Tabs defaultActiveKey="1">
          {
            getFieldValue('rollover_type') === 2
              ? null
              :
              (
                <TabPane tab={<IntlMessages id="jobCostRollovers.tab.item" />}
                  key="1"
                >
                  {
                    data.status === 1
                      ?
                      (
                        <Form.Item
                          {...formItemLayout}
                          //label={<IntlMessages id="jobCostRollovers.item" />}
                          extra={<IntlMessages id="jobCostRollovers.item.help" />}
                        // validateStatus={error.item_request_details ? "error" : null}
                        // help={
                        //   error.item_request_details ? error.item_request_details : null
                        // }
                        >
                          <Select
                            value={search ? search : []}
                            showSearch
                            allowClear
                            // optionFilterProp="children"
                            filterOption={false}
                            placeholder={
                              <IntlMessages id="selectItem" />
                            }
                            loading={loading}
                            notFoundContent={
                              loading
                                ?
                                (
                                  <Spin size="small" />
                                )
                                : getFieldValue('id_location') && getFieldValue('id_location_job_costing')
                                  ?
                                  (
                                    <Empty description={<IntlMessages id="noData" />} />
                                  )
                                  :
                                  (
                                    <Empty
                                      description={
                                        <IntlMessages id="jobCostRollover.item.noData" />
                                      }
                                    />
                                  )
                            }
                            onSelect={this.handleSelectItem}
                            onSearch={this.handleSearchItem}
                          >
                            {getFieldValue('id_location') && getFieldValue('id_location_job_costing')
                              ?
                              jobCostRollover.items.map(data => (
                                !this.state.jc_items.includes(data.id_item)
                                  ?
                                  (
                                    <Option key={data.id_item} value={data.id_item}>
                                      {data.item_code + " - " + data.item_name}
                                    </Option>
                                  )
                                  : null
                              ))
                              : null
                            }
                          </Select>
                        </Form.Item>
                      )
                      : null

                  }


                  <EditableTableItem
                    items={this.props.jobCostRollover.items}
                    projects={this.props.jobCostRollover.projects}
                    departments={this.props.jobCostRollover.departments}
                    temp_job_cost_rollover_detail={this.state.temp_job_cost_rollover_detail}
                    temp_job_cost_rollover_details={this.state.temp_job_cost_rollover_details}
                    job_cost_rollover_details={this.state.job_cost_rollover_details}
                    clearJobCostRolloverDetail={this.clearJobCostRolloverDetail}
                    clearJobCostRolloverDetails={this.clearJobCostRolloverDetails}
                    clear={this.state.clear}
                    handleSave={this.handleSave}
                    id_location={getFieldValue('id_location')}
                    status={data.status}
                    total_amount={getFieldValue('total_amount')}
                  />

                </TabPane>
              )
          }
          <TabPane tab={<IntlMessages id="jobCostRollovers.tab.expense" />}
            key="2"
          >
            {
              data.status === 1
                ?
                (
                  <Form.Item
                    {...formItemLayout}
                  //label={<IntlMessages id="GLAccount" />}
                  >
                    {getFieldDecorator("id_account_expense", {
                      // initialValue: data.id_gl_account,
                      // rules: [
                      //   {
                      //     required: true,
                      //     message: <IntlMessages id="required" />
                      //   }
                      // ]
                    })(
                      <Select
                        loading={this.props.jobCostRollover.loading}
                        showSearch
                        optionFilterProp="children"
                        notFoundContent={
                          loading
                            ?
                            (
                              <Spin size="small" />
                            )
                            : getFieldValue('id_location_job_costing')
                              ?
                              (
                                <Empty description={<IntlMessages id="noData" />} />
                              )
                              :
                              (
                                <Empty
                                  description={
                                    <IntlMessages id="jobCostRollover.import.first" />
                                  }
                                />
                              )
                        }
                        placeholder={
                          <IntlMessages id="selectExpense" />
                        }
                        onChange={this.handleChangeExpenseAccount}
                      >

                        {getFieldValue('id_location_job_costing')
                          ?
                          (
                            jobCostRollover.accounts.map((data, key) => (
                              data.id_account_type === "04" ||
                                data.id_account_type === "15" ||
                                data.id_account_type === "16" ||
                                data.id_account_type === "18"
                                ?
                                (
                                  <Option key={key} value={data.id_account}>
                                    {data.account_code + " (" + data.account_name + ")"}
                                  </Option>
                                )
                                : null
                            ))
                          )
                          : null
                        }
                      </Select>
                    )}
                  </Form.Item>
                )
                : null
            }


            <EditableTableExpense
              accounts={this.props.jobCostRollover.accounts}
              temp_expense={this.state.temp_expense}
              temp_expenses={this.state.temp_expenses}
              expenses={this.state.expenses}
              clearExpense={this.clearExpense}
              clearExpenses={this.clearExpenses}
              handleSave={this.handleSaveExpense}
              id_location={getFieldValue('id_location')}
              status={data.status}
              total_amount={getFieldValue('total_amount')}
            />

          </TabPane>
        </Tabs>

        <Row>
          <Col span={12} style={{ paddingLeft: "15px", marginTop: "25px" }}>

            {
              getFieldValue("rollover_type") === 1
                ?
                (
                  <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                    <Col span={14} style={{ marginTop: "5px", textAlign: "right", }}>
                      <strong>
                        <IntlMessages id="totalCostRatio" />
                      </strong>
                    </Col>
                    <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right", }}>
                      <span>
                        <NumberFormat
                          value={
                            isNaN(100 * this.state.total_cost / this.state.total_amount)
                              ? 0
                              : 100 * this.state.total_cost / this.state.total_amount
                          }
                        />
                        %
                          </span>
                    </Col>
                  </Row>
                )
                : null
            }

            <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
              <Col span={14} style={{ marginTop: "5px", textAlign: "right" }}>
                <strong>
                  <IntlMessages id="totalExpenseRatio" />
                </strong>
              </Col>
              <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                <span>
                  <NumberFormat
                    value={
                      isNaN(100 * this.state.total_expense / this.state.total_amount)
                        ? 0
                        : 100 * this.state.total_expense / this.state.total_amount
                    }
                  />
                  %
                </span>
              </Col>
            </Row>
            <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
              <Col span={14} style={{ marginTop: "5px", textAlign: "right" }}>
                <strong>
                  <IntlMessages id="totalAmountRatio" />
                </strong>
              </Col>
              <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                <span>
                  {
                    getFieldValue("rollover_type") === 1
                      ?
                      <NumberFormat
                        value={
                          isNaN(100 * (this.state.total_cost + this.state.total_expense) / this.state.total_amount)
                            ? 0
                            : 100 * (this.state.total_cost + this.state.total_expense) / this.state.total_amount
                        }
                      />
                      :
                      <NumberFormat
                        value={
                          isNaN(100 * this.state.total_expense / this.state.total_amount)
                            ? 0
                            : 100 * this.state.total_expense / this.state.total_amount
                        }
                      />
                  }
                  %
                </span>
              </Col>
            </Row>
          </Col>
          <Col span={12} style={{ paddingLeft: "15px", paddingRight: "50px", marginTop: "25px" }}>
            {
              getFieldValue("rollover_type") === 1
                ?
                (
                  <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
                    <Col span={14} style={{ marginTop: "5px", textAlign: "right", }}>
                      <strong>
                        <IntlMessages id="totalCost" />
                      </strong>
                    </Col>
                    <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right", }}>
                      <span>
                        <NumberFormat value={this.state.total_cost} />
                      </span>
                    </Col>
                  </Row>
                )
                : null
            }

            <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
              <Col span={14} style={{ marginTop: "5px", textAlign: "right" }}>
                <strong>
                  <IntlMessages id="totalExpense" />
                </strong>
              </Col>
              <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                <span>
                  <NumberFormat value={this.state.total_expense} />
                </span>
              </Col>
            </Row>
            <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
              <Col span={14} style={{ marginTop: "5px", textAlign: "right" }}>
                <strong>
                  <IntlMessages id="totalAmount" />
                </strong>
              </Col>
              <Col span={8} offset={1} style={{ marginTop: "5px", textAlign: "right" }}>
                <span>
                  {getFieldValue("rollover_type") === 1
                    ? <NumberFormat value={(this.state.total_cost + this.state.total_expense)} />
                    : <NumberFormat value={this.state.total_expense} />
                  }
                </span>
              </Col>
            </Row>
          </Col>
        </Row>


        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginRight: "5px" }}
            loading={loading}
            disabled={
              data.status === 1
                ? false
                : true
            }
          >
            <IntlMessages id="button.submit" />
          </Button>

          {/* Process */}
          <Popconfirm
            title={<IntlMessages id="confirmTransaction" />}
            okText={<IntlMessages id="yes" />}
            cancelText={<IntlMessages id="no" />}
            onConfirm={() =>
              processJobCostRollover(data.id_job_cost_rollover)
            }
          >
            <Button
              style={{ marginRight: "5px", padding: "0px 15px" }}
              disabled={
                data.status === 1 &&
                  permissions.includes("Confirm Job Costing")
                  ? false
                  : true
              }
              loading={loading}
            >
              <IntlMessages id="button.confirmTransaction" />
            </Button>
          </Popconfirm>

          {/* Cancel */}

          <Button
            style={{ marginRight: "5px", padding: "0px 15px" }}
            disabled={
              data.status === 2 &&
                permissions.includes("Cancel Job Costing")
                ? false
                : true
            }
            loading={loading}
            onClick={this.handleModalCancel}
          >
            <IntlMessages id="button.cancelTransaction" />
          </Button>

        </Form.Item>
      </Form>
    );
  }
}

const WrappedJobCostRolloverUpdateForm = Form.create({ name: "update_job_cost_rollover" })(JobCostRolloverUpdateForm);

export default WrappedJobCostRolloverUpdateForm;
